@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap);
*{box-sizing:border-box}body{font-size:.9rem;background-color:#fff}h1,h2,h3,h4,h5,h6{margin:0}span{font-size:.85rem;font-weight:400}
header{left:0;right:0;top:0;position:fixed;margin:auto;box-shadow:0 1px 0 0 rgba(0,0,0,.1);background-color:#fff;z-index:2}header .nav{display:flex;align-items:center;justify-content:center;font-weight:500;flex-wrap:wrap}header .nav .main-nav{cursor:pointer}header .nav .main-nav::after{content:"wanted";font-family:"Outfit",sans-serif;font-size:1.4rem;margin-left:.7rem}header .nav .menus{display:flex;list-style:none}header .nav .menus .home{display:none}header .nav .menus>*{padding:.5rem .7rem;cursor:pointer}header .nav [class*=fa]{font-size:1.2rem}@media(min-width: 766px)and (max-width: 989px){header .nav{width:90%;margin:auto;justify-content:space-between}header .nav .main-nav{width:100%;padding:.9rem 0}header .nav .menus{padding-left:0}}@media(max-width: 766px){header .nav{width:90%;margin:auto;justify-content:space-between}header .nav .main-nav{width:100%;padding:.9rem 0}header .nav .menus{padding-left:0}header .nav .menus .home{display:inline;display:initial;color:#258bf7;font-weight:700}header .nav .menus :not(:nth-child(1),:nth-child(2),:nth-child(3)){display:none}}
.main-container{padding-top:5.5rem}@media(min-width: 766px)and (max-width: 989px){.main-container{padding-top:9rem}}@media(max-width: 766px){.main-container{padding-top:9rem}}
.slider-container{display:flex;position:relative;width:100vw}.slider-container .slider-banner{position:relative;height:20rem}.slider-container .slider-banner.main{width:80%;margin:0 2rem}.slider-container .slider-banner:not(.main){width:10%;-webkit-filter:brightness(0.4);filter:brightness(0.4)}.slider-container .slider-banner img{width:100%;height:100%;object-fit:cover}.slider-container .slider-banner.main img{border-radius:1.2rem}.slider-container .slider-banner:not(.main):first-child img{border-top-right-radius:1.2rem;border-bottom-right-radius:1.2rem;object-position:right}.slider-container .slider-banner:not(.main):nth-child(3) img{border-top-left-radius:1.2rem;border-bottom-left-radius:1.2rem;object-position:left}@media(min-width: 766px)and (max-width: 989px){.slider-container .slider-banner{height:13rem}.slider-container .slider-banner.main{width:90%;margin:0 1.5rem}.slider-container .slider-banner:not(.main){width:5%}}@media(max-width: 766px){.slider-container .slider-banner{height:13rem}.slider-container .slider-banner.main{width:90%;margin:0 1rem}.slider-container .slider-banner:not(.main){width:5%}}
.move-button-container{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:0;right:0;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:1.5rem .5rem;background-color:#fff;border-radius:999px;font-size:1.2rem;opacity:.4;z-index:1;cursor:pointer}.move-button-container.left{margin-right:auto;margin-left:5%}.move-button-container.right{margin-left:auto;margin-right:5%}@media(min-width: 766px)and (max-width: 989px){.move-button-container{display:none}}@media(max-width: 766px){.move-button-container{display:none}}
.description-container{position:absolute;left:1.3rem;bottom:2rem;display:flex;flex-direction:column;justify-content:center;width:21rem;border-radius:5px;background-color:#fff;box-shadow:0 1px 0 0 rgba(0,0,0,.1)}.description-container .title{padding:1.6rem 1.4rem;line-height:1.5}.description-container .link{padding:1.4rem;font-weight:600;color:#258bf7;border-top:1px solid #ececec;cursor:pointer}.description-container .link::after{content:" >"}@media(min-width: 766px)and (max-width: 989px){.description-container{position:static;position:initial;box-shadow:none;box-shadow:initial;align-items:center;width:100%}.description-container .title{padding-bottom:0;text-align:center;font-size:.8rem}.description-container .link{padding:.9rem;border-top:0;border-top:initial}}@media(max-width: 766px){.description-container{position:static;position:initial;box-shadow:none;box-shadow:initial;align-items:center;width:100%}.description-container .title{padding-bottom:0;text-align:center;font-size:.7rem;white-space:nowrap}.description-container .link{padding:.9rem;border-top:0;border-top:initial}}
