@use "../globalStyles/_mixins" as m;

.slider-container {
  display: flex;
  position: relative;
  width: 100vw;

  .slider-banner {
    position: relative;
    height: 20rem;

    &.main {
      width: 80%;
      margin: 0 2rem;
    }

    &:not(.main) {
      width: 10%;
      filter: brightness(0.4);
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &.main img {
      border-radius: 1.2rem;
    }

    &:not(.main) {
      &:first-child img {
        border-top-right-radius: 1.2rem;
        border-bottom-right-radius: 1.2rem;
        object-position: right;
      }

      &:nth-child(3) img {
        border-top-left-radius: 1.2rem;
        border-bottom-left-radius: 1.2rem;
        object-position: left;
      }
    }
  }

  @include m.tablet {
    .slider-banner {
      height: 13rem;

      &.main {
        width: 90%;
        margin: 0 1.5rem;
      }

      &:not(.main) {
        width: 5%;
      }
    }
  }

  @include m.mobile {
    .slider-banner {
      height: 13rem;

      &.main {
        width: 90%;
        margin: 0 1rem;
      }

      &:not(.main) {
        width: 5%;
      }
    }
  }
}
